'use client';

import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Sparkles, Gamepad2, ArrowRight, Phone, Lock, User, Mail, AlertCircle } from 'lucide-react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';

export default function LoginPage() {
  const { user, loading, signInWithGoogle, signInWithAnonymously, loginWithEmail, registerWithEmail } = useAuth();
  const router = useRouter();

  // Mode: LOGIN (Phone) or REGISTER (Email)
  const [authMethod, setAuthMethod] = useState<'PHONE' | 'EMAIL'>('PHONE');
  const [isRegister, setIsRegister] = useState(false);

  // Form Data
  const [phoneNumber, setPhoneNumber] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [displayName, setDisplayName] = useState('');

  const [isLoading, setIsLoading] = useState(false);
  const [errorMsg, setErrorMsg] = useState('');

  useEffect(() => {
    if (user && !loading) router.push('/dashboard');
  }, [user, loading, router]);

  // --- Handlers ---

  const handlePhoneLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!phoneNumber) return setErrorMsg("กรุณากรอกเบอร์โทรศัพท์");

    setIsLoading(true);
    setErrorMsg('');

    try {
      // In a real app with Firebase Phone Auth, this would send an SMS.
      // Since we want to link with the 'Synced DB' quickly, we use Anonymous Auth 
      // and let the Dashboard logic match the phone number.
      await signInWithAnonymously();
      localStorage.setItem('user_phone_claim', phoneNumber); // Save claim for linking
      // AuthContext listener will handle redirect
    } catch (err: any) {
      setErrorMsg(err.message);
      setIsLoading(false);
    }
  };

  const handleEmailAuth = async (e: React.FormEvent) => {
    e.preventDefault();
    setErrorMsg('');
    setIsLoading(true);

    try {
      if (isRegister) {
        if (!displayName) throw new Error("กรุณากรอกชื่อผู้ใช้งาน");
        await registerWithEmail(email, password, displayName);
      } else {
        await loginWithEmail(email, password);
      }
    } catch (err: any) {
      let msg = err.message;
      if (msg.includes("auth/email-already-in-use")) msg = "อีเมลนี้มีผู้ใช้งานแล้ว";
      if (msg.includes("auth/wrong-password")) msg = "รหัสผ่านไม่ถูกต้อง";
      if (msg.includes("auth/user-not-found")) msg = "ไม่พบผู้ใช้งาน";
      setErrorMsg(msg);
      setIsLoading(false);
    }
  };

  const handleGoogle = async () => {
    try {
      await signInWithGoogle();
    } catch (err: any) {
      setErrorMsg(err.message);
    }
  };

  return (
    <div className="min-h-screen bg-[#0f172a] flex flex-col items-center justify-center p-6 relative overflow-hidden font-poppins">

      {/* Background FX */}
      <div className="absolute top-0 left-0 w-full h-full bg-[radial-gradient(ellipse_at_top,_var(--tw-gradient-stops))] from-blue-900/40 via-[#0f172a] to-[#0f172a]" />
      <div className="absolute top-[-10%] right-[-10%] w-[500px] h-[500px] bg-purple-600/10 rounded-full blur-[120px] pointer-events-none" />
      <div className="absolute bottom-[-10%] left-[-10%] w-[500px] h-[500px] bg-blue-600/10 rounded-full blur-[120px] pointer-events-none" />

      {/* Main Card */}
      <motion.div
        initial={{ opacity: 0, scale: 0.95, y: 20 }}
        animate={{ opacity: 1, scale: 1, y: 0 }}
        className="w-full max-w-md bg-[#1e293b]/80 backdrop-blur-xl border border-white/10 shadow-2xl rounded-3xl overflow-hidden relative z-10"
      >
        {/* Header */}
        <div className="p-8 pb-4 text-center">
          <div className="w-16 h-16 bg-gradient-to-br from-blue-600 to-indigo-600 rounded-2xl mx-auto flex items-center justify-center shadow-lg shadow-blue-500/40 mb-4">
            <Gamepad2 className="w-8 h-8 text-white" />
          </div>
          <h1 className="text-2xl font-bold text-white tracking-tight">Up Level Guild</h1>
          <p className="text-slate-400 text-sm mt-1">Member Portal</p>
        </div>

        {/* Auth Method Tabs */}
        <div className="grid grid-cols-2 border-b border-white/5">
          <button
            onClick={() => setAuthMethod('PHONE')}
            className={`py-4 text-sm font-bold transition-all relative ${authMethod === 'PHONE' ? 'text-blue-400' : 'text-slate-500 hover:text-slate-300'}`}
          >
            <Phone className="w-4 h-4 inline-block mr-2" /> เบอร์โทรศัพท์
            {authMethod === 'PHONE' && <motion.div layoutId="tab" className="absolute bottom-0 left-0 w-full h-0.5 bg-blue-500" />}
          </button>
          <button
            onClick={() => setAuthMethod('EMAIL')}
            className={`py-4 text-sm font-bold transition-all relative ${authMethod === 'EMAIL' ? 'text-blue-400' : 'text-slate-500 hover:text-slate-300'}`}
          >
            <Mail className="w-4 h-4 inline-block mr-2" /> อีเมล / รหัสผ่าน
            {authMethod === 'EMAIL' && <motion.div layoutId="tab" className="absolute bottom-0 left-0 w-full h-0.5 bg-blue-500" />}
          </button>
        </div>

        <div className="p-8 pt-6">

          {/* LOGIN METHOD: PHONE */}
          {authMethod === 'PHONE' && (
            <motion.form
              initial={{ opacity: 0, x: -20 }} animate={{ opacity: 1, x: 0 }}
              onSubmit={handlePhoneLogin} className="space-y-4"
            >
              <div className="space-y-2">
                <label className="text-xs font-bold text-slate-400 uppercase">เบอร์โทรศัพท์มือถือ</label>
                <div className="relative">
                  <Phone className="absolute left-3 top-3.5 text-slate-500 w-5 h-5" />
                  <input
                    type="tel"
                    placeholder="08X-XXX-XXXX"
                    value={phoneNumber} onChange={e => setPhoneNumber(e.target.value)}
                    className="w-full bg-[#0f172a] border border-slate-700 rounded-xl py-3 pl-10 pr-4 text-white font-mono focus:border-blue-500 outline-none transition-all placeholder-slate-600"
                  />
                </div>
              </div>

              <button disabled={isLoading} className="w-full py-3.5 bg-gradient-to-r from-blue-600 to-indigo-600 text-white font-bold rounded-xl shadow-lg flex items-center justify-center gap-2 hover:from-blue-500 hover:to-indigo-500 active:scale-[0.98] transition-all">
                {isLoading ? 'Loading...' : 'เข้าสู่ระบบ'} <ArrowRight className="w-5 h-5" />
              </button>

              <div className="pt-4 border-t border-white/5 mt-4">
                <button type="button" onClick={handleGoogle} className="w-full py-3 bg-white hover:bg-gray-100 text-slate-900 font-bold rounded-xl flex items-center justify-center gap-2 transition-all">
                  <span className="text-blue-500 font-black text-lg">G</span> เข้าสู่ระบบด้วย Google
                </button>
              </div>
            </motion.form>
          )}

          {/* LOGIN METHOD: EMAIL */}
          {authMethod === 'EMAIL' && (
            <motion.form
              initial={{ opacity: 0, x: 20 }} animate={{ opacity: 1, x: 0 }}
              onSubmit={handleEmailAuth} className="space-y-4"
            >
              {isRegister && (
                <div className="relative">
                  <User className="absolute left-3 top-3.5 text-slate-500 w-5 h-5" />
                  <input
                    type="text" placeholder="ชื่อผู้ใช้งาน (Display Name)"
                    value={displayName} onChange={e => setDisplayName(e.target.value)}
                    className="w-full bg-[#0f172a] border border-slate-700 rounded-xl py-3 pl-10 pr-4 text-white text-sm focus:border-blue-500 outline-none"
                  />
                </div>
              )}

              <div className="relative">
                <Mail className="absolute left-3 top-3.5 text-slate-500 w-5 h-5" />
                <input
                  type="email" placeholder="อีเมล (Email)"
                  value={email} onChange={e => setEmail(e.target.value)}
                  className="w-full bg-[#0f172a] border border-slate-700 rounded-xl py-3 pl-10 pr-4 text-white text-sm focus:border-blue-500 outline-none"
                />
              </div>

              <div className="relative">
                <Lock className="absolute left-3 top-3.5 text-slate-500 w-5 h-5" />
                <input
                  type="password" placeholder="รหัสผ่าน (Password)"
                  value={password} onChange={e => setPassword(e.target.value)}
                  className="w-full bg-[#0f172a] border border-slate-700 rounded-xl py-3 pl-10 pr-4 text-white text-sm focus:border-blue-500 outline-none"
                />
              </div>

              <button disabled={isLoading} className="w-full py-3.5 bg-blue-600 hover:bg-blue-500 text-white font-bold rounded-xl shadow-lg transition-all active:scale-[0.98]">
                {isLoading ? 'Loading...' : (isRegister ? 'สมัครสมาชิก' : 'เข้าสู่ระบบ')}
              </button>

              <p className="text-center text-xs text-slate-400 mt-2 cursor-pointer hover:text-white transition-colors" onClick={() => setIsRegister(!isRegister)}>
                {isRegister ? 'มีบัญชีอยู่แล้ว? เข้าสู่ระบบ' : 'ยังไม่มีบัญชี? สมัครสมาชิก'}
              </p>
            </motion.form>
          )}

          {/* Error Msg */}
          {errorMsg && (
            <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="mt-4 p-3 bg-red-500/10 border border-red-500/20 rounded-lg flex items-center gap-2 text-xs text-red-300">
              <AlertCircle className="w-4 h-4 shrink-0" /> {errorMsg}
            </motion.div>
          )}

        </div>
      </motion.div>
    </div>
  );
}
